.test {
  background-color: #be3455;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  max-width: 640px;
  width: 100%;
  min-height: 320px;
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
  transition: transform .2s ease-in-out;
  transform: scale(1.1);
  box-shadow: 0 1px 3px 5px rgba(0, 0, 0, .2), 0 1px 3px rgba(0, 0, 0, .14), 0 2px 1px 2px rgba(0, 0, 0, .12);
}

